.overlay {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.content {
    background-color: #fff;
    padding: 30px;
    border-radius: 10px;
    min-width: 300px;
    max-width: 700px;
    position: relative;
}

.close {
    font-size: 20px;
    font-weight: bold;
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    cursor: pointer;
    color: #303030;
    position: absolute;
    top: 10px;
    right: 10px;
}