.form {
    display: flex;
    width: 600px;
    margin: 0 auto;
    margin-bottom: 40px;
  }

  .input {
    height: 40px;
    flex-grow: 1;
    font-size: 18px;
    padding-left: 15px;
    border: 1px solid #303030;
  }

  .btn {
  font-size: 14px;
  padding: 10px 20px;
  background-color: #c576b4;;
  color: #ffffff;
  border: none;
  text-transform: uppercase;
  cursor: pointer;
  margin-left: 10px;
  transition: background-color 250ms linear;
}